class Book {
  key: string;
  name: string;
  author: string;
  description: string;
  md5: string;
  cover: string;
  format: string;
  publisher: string;
  constructor(
    key: string,
    name: string,
    author: string,
    description: string,
    md5: string,
    cover: string,
    format: string,
    publisher: string
  ) {
    this.key = key; // 
    this.name = name; // 
    this.author = author; // 
    this.description = description; // 
    this.md5 = md5; //epubmd5
    this.cover = cover;
    this.format = format;
    this.publisher = publisher;
  }
}

export default Book;
